.white-space-nowrap {
  white-space: nowrap;
}
.table {
  > thead {
    > tr {
      > th {
        padding-top: 0.625rem;
        padding-bottom: 0.625rem;
        line-height: 1;

        &.sort {
          white-space: nowrap;
          cursor: pointer;
          &::after {
            content: url('data:image/svg+xml;base64,');
            margin-left: 0.25rem;
          }
        }
        &.asc {
          &::after {
            content: url('data:image/svg+xml;base64,') !important;
            margin-top: -1px !important;
          }
        }
        &.desc {
          &::after {
            content: url('data:image/svg+xml;base64,') !important;
            // margin-top: -1px !important;
          }
        }
      }
    }
  }
  &.phoenix-table {
    margin-bottom: 0 !important;
    > thead {
      > tr {
        > th {
          vertical-align: middle !important;
          font-size: map-get($font-sizes, 9);
          text-transform: uppercase;
          &:first-child {
            padding-left: 0;
          }
          &:last-child {
            padding-right: 0;
          }
        }
      }
    }
    > tbody {
      > tr {
        > td {
          vertical-align: middle !important;
          &:first-child {
            padding-left: 0;
          }
          &:last-child {
            padding-right: 0;
          }
        }
      }
    }
  }
  &.phoenix-table-bordered {
    > :not(caption) > * {
      border-width: $table-border-width 0;
      > td,
      th {
        &:not(:first-child, :last-child) {
          border-width: 0 $table-border-width;
          border-color: var(--#{$prefix}gray-300);
        }
      }
    }
  }

  // table varient
  @each $color, $value in $theme-colors {
    @if $color == 'dark' {
      .table-#{$color},
      &.table-#{$color} {
        --#{$prefix}table-hover-bg: rgba(
          var(--#{$prefix}primary-600-rgb),
          0.07
        );
        color: $gray-400;
      }
    } @else {
      .table-#{$color},
      &.table-#{$color} {
        color: $body-color;
        border-color: $gray-300;
      }
    }
  }
}

.table {
  > tbody {
    > tr {
      > td {
        padding-top: 0px !important;
        padding-bottom: 0px !important;
        vertical-align: middle;
        &:first-child {
          padding-left: 0;
        }
        &:last-child {
          padding-right: 0;
        }
      }
    }
  }
}
