.nav-tabs {
  .nav-link {
    padding: 0.5rem;
    margin-bottom: -$nav-tabs-border-width;
    font-weight: $font-weight-bold;
    font-size: map-get($font-sizes, 9);
    // background-color: transparent;
    @include border-top-radius($nav-tabs-border-radius);
    &.disabled {
      color: var(--#{$prefix}emphasis-color);
      background-color: transparent;
      border-color: transparent;
    }
  }
}
.nav-tabs {
  border-bottom: 0px solid transparent;
  .nav-link {
    border: none;
    padding: 0.5rem 0.5rem;
    color: var(--#{$prefix}primary);
  }

  .nav-link.active,
  .nav-item.show .nav-link {
    color: var(--#{$prefix}emphasis-color);
    background-color: transparent;
  }
}

.nav-phoenix-pills {
  background: var(--#{$prefix}body-highlight-bg);
  border: 1px solid var(--#{$prefix}border-color);
  border-radius: 0.5rem;
  .nav-item {
    flex: 1;
    .nav-link {
      color: var(--#{$prefix}tertiary-color);
      font-weight: $font-weight-semibold;
      line-height: $line-height-sm;
      text-align: center;
      padding-top: 0.25rem;
      padding-bottom: 0.25rem;
      &.active {
        background: var(--#{$prefix}emphasis-bg);
        color: var(--#{$prefix}primary);
        border-left: 1px solid var(--#{$prefix}border-color);
        border-right: 1px solid var(--#{$prefix}border-color);
        border-radius: 0.5rem;
      }
    }
    &:last-child {
      .nav-link {
        &.active {
          border-right: none;
        }
      }
    }
    &:first-child {
      .nav-link {
        &.active {
          border-left: none;
        }
      }
    }
  }
}

.nav-underline {
  .nav-link {
    color: var(--#{$prefix}body-color);
    font-weight: $font-weight-bold;
    font-size: map-get($font-sizes, 9);
  }
}

.nav-links {
  .nav-link {
    font-weight: $font-weight-bold;
    font-size: map-get($font-sizes, 9);
    &:hover {
      span:first-child {
        text-decoration: underline !important;
      }
    }
    &.active {
      color: var(--#{$prefix}emphasis-color);
    }
  }
}

// FAQ
.faq-category-tab {
  position: sticky;
  top: var(--#{$prefix}navbar-top-height);
  z-index: 1020;
  @include media-breakpoint-down(md) {
    top: 0;
  }
  .nav-item {
    flex-basis: 50%;
  }
  .nav-link {
    color: var(--#{$prefix}gray-400);
    width: 100%;
    &.active {
      color: var(--#{$prefix}primary);
      border-bottom: 3px solid var(--#{$prefix}primary);
    }
  }
}
.faq-subcategory-tab {
  .nav-link {
    border: 1px solid var(--#{$prefix}gray-200);
    &:hover {
      border: 1px solid var(--#{$prefix}gray-400);
    }
    &.active {
      border: 1px solid var(--#{$prefix}primary) !important;
      .category-icon {
        color: var(--#{$prefix}primary) !important;
      }
    }
  }
  @include media-breakpoint-up(md) {
    min-height: 100%;
    height: 1px;
  }
}
.empty-header {
  height: 60px;
  position: sticky;
  top: var(--#{$prefix}navbar-top-height);
  background-color: var(--#{$prefix}body-bg);
  z-index: 1;
}

